/* src/styles.css */
body {
    font-family: Arial, sans-serif;
    background: #fff;
    margin: 0;
    padding: 0;
  }
  
  .calendar-header {
    padding: 1.5rem;
    border-bottom: 1px solid #ddd;
  }
  
  .month-year {
    display: flex;
    align-items: center;
    gap: 1rem;
  }
  
  .month-year h2 {
    margin: 0;
    font-size: 2rem;
  }
  
  .nav-button {
    background: #fff;
    border: 1px solid #ccc;
    padding: 0.5rem;
    cursor: pointer;
    font-size: 1rem;
    border-radius: 6px;
  }
  
  .subheading {
    margin-top: 0.5rem;
    color: #666;
  }
  
  .day-labels {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    margin-top: 1.5rem;
    color: #888;
    font-weight: bold;
    text-align: center;
    padding: 0.5rem 0;
  }
  
  .calendar-grid {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    padding: 1rem;
  }
  
  .week {
    display: contents;
  }
  
  .day {
    border: 1px solid #eee;
    min-height: 120px;
    padding: 0.5rem;
    position: relative;
    font-size: 0.85rem;
    background: #fff;
    color: #333;
  }
  
  .date {
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
  
  .today {
    border: 2px solid #007bff;
    background-color: #e6f2ff;
  }

  .outside-month {
    background-color: #f5f5f5;
    color: #aaa;
  }
  
  .event {
    background-color: #ccc;
    color: #000;
    padding: 0.25rem;
    margin-top: 0.25rem;
    border-radius: 4px;
    font-size: 0.75rem;
    line-height: 1.2;
  }
  
  .event-title {
    font-weight: bold;
  }
  
  .event-time {
    font-size: 0.7rem;
    color: #333;
  }
  